<template>
    <div class='chart' id='chart'></div>
</template>
<script>
import * as echarts from 'echarts'
import { get } from '../utils/request.js'

export default {
  name: 'ScatterChart',
  data () {
    return {
      locations: [[1.2, 2.3], [2.3, 4.4], [3.5, 4.5]]
    }
  },
  created () {
    this.timer()
    this.getLocations()
  },
  setup () {
  },
  mounted () {
    this.initChart()
  },
  methods: {
    timer () {
      return setInterval(() => {
        this.getLocations()
      }, 1000)
    },
    async getLocations () {
      const result = await get('/api/location/get?time_stamp=1621581570000&scene_id=1&user_id=1&frame_size=10')
      // 过滤器
      if (result?.code === 200 && result?.data) {
        this.locations = result.data.list[0].locations.map((item) => {
          return item.coordinate.split(',').map(Number)
        })
      }
    },
    initChart () {
      const myChart = echarts.init(document.getElementById('chart'))
      var option = {
        title: {
          text: '实验地点：XXX',
          left: 'center',
          top: '15px'
        },
        xAxis: {
          type: 'value',
          min: 0,
          max: 50
        },
        yAxis: {
          type: 'value',
          min: 0,
          max: 50
        },
        dataZoom: [
          { // 这个dataZoom组件，默认控制x轴。
            type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
            start: 0, // 左边在 10% 的位置。
            end: 20 // 右边在 60% 的位置。
          },
          { // 这个dataZoom组件，也控制x轴。
            type: 'inside', // 这个 dataZoom 组件是 inside 型 dataZoom 组件
            start: 0, // 左边在 10% 的位置。
            end: 20 // 右边在 60% 的位置。
          },
          {
            type: 'slider',
            yAxisIndex: 0,
            start: 0,
            end: 20
          },
          {
            type: 'inside',
            yAxisIndex: 0,
            start: 0,
            end: 20
          }
        ],
        series: [
          {
            type: 'scatter',
            itemStyle: {
              opacity: 0.8
            },
            symbol: 'pin',
            symbolSize: 25,
            data: [[1.1, 1.2],
              [3.4, 5.6]]
          }
        ]
      }
      myChart.setOption(option)
      // 随着屏幕大小调节图表
      window.addEventListener('resize', () => {
        myChart.resize()
      })
      setInterval(() => {
        myChart.setOption({
          series: [{
            data: this.locations
          }]
        })
      }, 1000)
    }
  }
}
</script>

<style lang="scss" scoped>
.chart{
    margin: 0 auto;
    width: 100%;
    height: 100%;
}
</style>
